<template>

            <v-layout align-center justify-center>
               <v-flex xs12 sm8 md4>
                  <v-card class="elevation-12">
                     <v-toolbar dark color="primary">
                        <v-toolbar-title class="flex text-center">MCP</v-toolbar-title>
                     </v-toolbar>
                     <v-card-text>
                     <form ref="form" @submit.prevent="login">
                            <v-text-field
                              v-model="username"
                              name="username"
                              :label="$vuetify.lang.t('$vuetify.login_name')"
                              type="text"
                              :placeholder="$vuetify.lang.t('$vuetify.login_name')"
                              required
                           ></v-text-field>
                           
                            <v-text-field
                              v-model="password"
                              name="password"
                              :label="$vuetify.lang.t('$vuetify.password')"
                              type="password"
                              :placeholder="$vuetify.lang.t('$vuetify.password')"
                              required
                           ></v-text-field>
                           <div class="text-center">
                            <v-btn  
                                type="submit"
                                class="mt-4" 
                                color="primary"
                            >
                                {{ $vuetify.lang.t('$vuetify.login_button') }}
                            </v-btn>
                           </div>
                    </form>
                     </v-card-text>
                  </v-card>
                
               </v-flex>
            </v-layout>

</template>

<script>
import { mapMutations } from "vuex";

export default {
  data: () => {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    ...mapMutations(["setUser", "setToken"]),
    async login(e) {
          e.preventDefault();
          const response = await fetch("http://192.168.12.159:8003/api.php/user", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              username: this.username,
              password: this.password,
            }),
          });
          const { user, token } = await response.json();
          localStorage.setItem('jwt_token', token);
          this.$router.push({name:'Home'});

        },
  },

};
</script>